.content-container {
  width: 100%;
  height: 80vh;
  background: #90c2e7;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.content-container > * {
  padding: 2rem;
}

@media screen and (min-width: 600px) {
  .content-container {
    overflow: hidden;
  }
}
